import { useDispatch, useSelector } from 'react-redux';
import { Link, NavLink } from 'react-router-dom';
import { login, logout } from '../store/authSlice';
import { useLocation } from 'react-router-dom/cjs/react-router-dom.min';
import { IoLogInOutline, IoLogOutOutline } from 'react-icons/io5';

const NavBar = () => {
    const isLoggedIn = useSelector((state) => state.auth.isLoggedIn);
    const dispatch = useDispatch();
    const location = useLocation();

    return (
        <header>
            <nav>
                {location.pathname !== '/' && (
                    <ul>
                        <li className="nav-item">
                            <NavLink to="/blogs" activeClassName="active" className="nav-link" aria-current="page">
                                BLOG
                            </NavLink>
                        </li>
                        <li>
                            {isLoggedIn && (
                                <NavLink to="/admin" activeClassName="active" className="nav-link" aria-current="page">
                                    Admin
                                </NavLink>
                            )}
                        </li>
                    </ul>
                )}
            </nav>
            <h1>
                <Link to="/">Home</Link>
            </h1>
            {location.pathname !== '/' && (
                <button
                    className="login-btn"
                    onClick={() => {
                        if (isLoggedIn) {
                            dispatch(logout());
                        } else {
                            dispatch(login());
                        }
                    }}
                >
                    {isLoggedIn ? <IoLogOutOutline /> : <IoLogInOutline />}
                </button>
            )}
        </header>
    );
};

export default NavBar;
