import { Link } from 'react-router-dom';
import BlogList from '../components/BlogList';

const AdminPage = () => {
    return (
        <div>
            <div className="head">
                <h2>Admin</h2>
                <div>
                    <Link to="/blogs/create" className="btn">
                        Create New
                    </Link>
                </div>
            </div>
            <div>
                <BlogList isAdmin={true} />
            </div>
        </div>
    );
};

export default AdminPage;
