import { IoLogoInstagram, IoMailOutline, IoLogoTwitter, IoLogoYoutube } from 'react-icons/io5';
import profile from '../img.jpg';

export default function BlogProfile() {
    return (
        <div className="profile">
            <img src={profile} />
            <p>소개글</p>
            <div className="sns-icons">
                <button>
                    <IoLogoInstagram />
                </button>
                <button>
                    <IoMailOutline />
                </button>
                <button>
                    <IoLogoTwitter />
                </button>
                <button>
                    <IoLogoYoutube />
                </button>
            </div>
        </div>
    );
}
